@use 'base/mixins' as m;
@use 'base/variables' as v;

.home {
    &__barra {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 1.5rem 3rem 1rem 3rem;
        background-image: linear-gradient(to bottom, rgba(v.$grisAzul, 1), rgba(v.$naranjaOscuro, 0.3));

        background-color: v.$grisAzul;
        box-shadow: inset 0px 3px 8px rgba(v.$naranjaOscuro, .5);
        box-shadow: inset 0px -3px 8px rgba(v.$naranjaOscuro, .5);

        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;

        }

        h1 {
            margin-bottom: 1rem;
            color: v.$blanco;
            font-size: 4rem;


            @include m.tablet {
                font-size: 6rem;
            }
        }
    }

    &__navegacion {

        // Menu mobile - Ocultar menu
        height: 0;
        visibility: hidden;
        opacity: 0;
        transition: visibility .3s ease-in-out, opacity .5s linear, height .5s linear;

        // Mostrar menu
        @include m.tablet {
            display: flex;
            align-items: center;
            opacity: 1;
            visibility: visible;
        }

        a {
            color: v.$blanco;
            text-decoration: none;
            font-size: 2rem;
            display: block; // ocupe todo el espacio disponible
            text-align: center; // centra el contenido
            padding: 1rem;
            text-transform: uppercase;
            font-weight: 900;


            &:hover {
                color: v.$naranjaOscuro;
            }

            @include m.tablet {
                margin-right: 2rem;
                font-size: 1.8rem;

                &:last-of-type {
                    margin-right: 0;
                }
            }
        }
    }

}

.home__derecha {

    display: none;

    @include m.tablet {
        align-items: flex-end;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}



// Menu de hamburguesa

.home__mobile-menu {

    @include m.tablet {
        display: none; // para que el vector no sea visible en pantallas grandes
    }
    img {
        width: 5rem;
    }
    &:hover {
        cursor: pointer;
    }
}

