@use 'base/mixins' as m;
@use 'base/variables' as v;

.no-proyectos {

    text-align: center;
    margin-top: 3rem;
    list-style: none;

    a {
        color: v.$indigo;
        font-weight: bold;
    }
}

.listado-proyectos {
    list-style: none;
    padding: 0;
    margin-top: 3rem;

    @include m.grid(1, 2rem);

    @include m.tablet {
        @include m.grid(2, 2rem);
    }

    @include m.desktop {
        @include m.grid(3, 2rem);
    }

    .proyecto {
        background-color: lighten(v.$indigo, 5%);
        min-height: 12rem;
        display: flex;
        align-items: center;
        padding: 2rem;
        line-height: 2rem;
        border-radius: .5rem;
        justify-content: space-between;

        .proyecto-elementos {
            display: flex;
            flex-direction: column;
            width: 100%;
            align-items: center;

            .numero-tareas {
                align-items: flex-end;
                font-size: 1.5rem;
            }
        }

    
        &:hover {
            background-color: darken(v.$indigo, 5%);
        }

        p {
            font-size: 2.5rem;
            color: v.$blanco;
            margin: 0;
        }
    }
    .proyecto-completo{
        background-image: url(../img/award.svg);
        background-repeat: no-repeat;
        background-size: 5rem;
        background-position: top right;






        border: 6px solid v.$naranja;

    }

}