html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.descripcion-pagina {
  text-align: center;
}

/** Media Queries **/
.auth__formulario-boton {
  text-align: center;
  display: flex;
  justify-content: center;
  border-radius: 2rem;
}

.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
  flex-direction: row;
}
.formulario .campo:last-of-type {
  margin-bottom: 4rem;
}
.formulario .campo label {
  flex: 0 0 4rem;
  margin: 0 3rem 0 1rem;
}
@media (min-width: 768px) {
  .formulario .campo label {
    flex: 0 0 7rem;
  }
}
.formulario .campo input {
  border-radius: 1rem;
  flex: 1;
  padding: 1.3rem;
  border: none;
  background-color: #d8dadf;
}

.acciones {
  margin-top: 5rem;
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #000;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}
.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: bold;
  margin: 1rem 0;
  font-size: 1.2rem;
  max-width: 80rem;
}
.alerta.error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alerta.exito {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

.auth__contenedor {
  display: flex;
  gap: 0;
  flex-direction: column;
  margin: 8rem auto;
  width: auto;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .auth__contenedor {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 5rem;
  }
}
.auth__imagen--login {
  margin: 0;
  min-height: 30rem;
  background-image: image-set(url(../img/puzzle.avif) 1x, url(../img/puzzle.webp) 1x, url(../img/puzzle.jpg) 1x);
  background-repeat: no-repeat;
  background-size: 300px;
  background-position: bottom center;
}
.auth__imagen--login h2 {
  font-size: clamp(2rem, -0.8rem + 10vw, 8rem);
  margin: 0;
  color: #385964;
  text-align: center;
}
@media (min-width: 768px) {
  .auth__imagen--login {
    background-size: 700px;
    min-height: 60rem;
    background-position: bottom left;
    text-align: left;
    margin: 2rem 0 2rem 0;
  }
}
.auth__formulario {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.auth__formulario .TaskTrack {
  background: linear-gradient(to right, #385964 0%, #7ea9b7 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 4rem;
  display: none;
}
@media (min-width: 768px) {
  .auth__formulario .TaskTrack {
    font-size: 7rem;
    display: block;
  }
}
.auth__formulario .tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .auth__formulario .tagline {
    font-size: 3rem;
  }
}

.login {
  margin-top: 0;
}
@media (min-width: 768px) {
  .login {
    margin-top: 5rem;
  }
}
.login input[type=submit] {
  background-color: #ff735c;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 1.5rem;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #ff4729;
  cursor: pointer;
}
.login .acciones a {
  color: #ff735c;
  margin: 2rem;
}
.login .acciones a:hover {
  color: #385964;
}

.auth__contenedor--crear {
  max-width: 95%;
}
.auth__contenedor--crear input[type=submit] {
  background-color: #ff735c;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin-top: 4rem;
  border-radius: 1.5rem;
}
@media (min-width: 768px) {
  .auth__contenedor--crear input[type=submit] {
    width: auto;
  }
}
.auth__contenedor--crear input[type=submit]:hover {
  background-color: #ff4729;
  cursor: pointer;
}
.auth__contenedor--crear .contenedor-sm {
  width: 100%;
  align-items: start;
}
@media (min-width: 768px) {
  .auth__contenedor--crear .contenedor-sm {
    padding-top: 5rem;
  }
}
.auth__contenedor--crear .acciones {
  display: flex;
