}
.auth__contenedor--crear .acciones a:hover {
  color: #385964;
}
@media (min-width: 768px) {
  .auth__contenedor--crear {
    display: block;
  }
}
.auth__imagen--crear {
  margin: 0 auto;
  min-height: 30rem;
  background-image: url(../img/listatildesNaranjas.svg);
