.sidebar {
  opacity: 0;
  background-color: #4338CA;
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 600ms;
}
.sidebar.mostrar {
  height: 100%;
  padding: 2rem;
  opacity: 1;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    opacity: 1;
    background-color: #4338CA;
    padding: 3rem;
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .contenedor-sidebar .cerrar-menu {
  display: flex;
  justify-content: center;
  height: 3rem;
  width: 3rem;
}
.sidebar .contenedor-sidebar .cerrar-menu img:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar .cerrar-menu {
    display: none;
  }
}
.sidebar .contenedor-sidebar h2 {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 4rem;
  text-align: center;
  padding: 0.7rem;
  transition-property: background-color;
  transition-duration: 300ms;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2.4rem;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #28217b;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 1.6rem;
